* {
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__hand {
  position: absolute;
}

.stopwatch__hand--minutes {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite clock;
  top: 20vmin;
}

.stopwatch__hand--seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite clock;
  top: 2vmin;
}

.stopwatch--speed-up .stopwatch__hand--minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--seconds {
  animation-duration: 10s;
}

@keyframes clock {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}
